@font-face {
  font-family: "volt";
  src: url("/static/fonts/Voltage Bold.otf"); }

@font-face {
  font-family: "raleway-extrabold";
  src: url("/static/fonts/Raleway-ExtraBold.ttf"); }

@font-face {
  font-family: "raleway-light";
  src: url("/static/fonts/Raleway-Light.ttf"); }

@font-face {
  font-family: "raleway-regular";
  src: url("/static/fonts/Raleway-Regular.ttf"); }

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  overflow: hidden; }
  .footer img {
    width: 100%;
    transform: translateY(10px);
    z-index: 9999; }
  .footer .footer_background {
    padding-top: 100px;
    background: #002d49;
    width: 100%;
    height: 100%; }
    .footer .footer_background .footer_container {
      max-width: 1200px;
      margin: auto;
      display: flex;
      justify-content: center;
      align-items: center; }
      .footer .footer_background .footer_container .site_map {
        display: flex;
        font-family: "raleway-light";
        color: white;
        font-size: 14px;
        line-height: 2;
        letter-spacing: 1px; }
        .footer .footer_background .footer_container .site_map .site_map_column {
          padding-right: 50px; }
          .footer .footer_background .footer_container .site_map .site_map_column .bold {
            font-family: "raleway-extrabold"; }
      .footer .footer_background .footer_container .footer_connect {
        color: white;
        padding-left: 100px; }
        .footer .footer_background .footer_container .footer_connect h3 {
          font-family: "volt", sans-serif;
          font-size: 32px;
          margin-bottom: 10px; }
        .footer .footer_background .footer_container .footer_connect .news_letter .signup_form {
          display: flex;
          margin-bottom: 20px; }
          .footer .footer_background .footer_container .footer_connect .news_letter .signup_form input {
            font-family: "raleway-regular";
            font-size: 16px;
            width: 300px;
            padding: 20px 0 20px 15px;
            border-style: none;
            border: none;
            height: 56px; }
          .footer .footer_background .footer_container .footer_connect .news_letter .signup_form button {
            font-family: "raleway-extrabold";
            font-size: 16px;
            width: 100px;
            height: 56px;
            border-style: none;
            background: #76b3a8;
            color: white; }
          .footer .footer_background .footer_container .footer_connect .news_letter .signup_form span {
            border-top: 28px solid #76b3a8;
            border-bottom: 28px solid #76b3a8;
            border-right: 10px solid transparent;
            height: 0;
            width: 0; }
        .footer .footer_background .footer_container .footer_connect .connect {
          display: flex; }
          .footer .footer_background .footer_container .footer_connect .connect p {
            font-family: "raleway-regular";
            font-size: 16px;
            line-height: 1.5; }
          .footer .footer_background .footer_container .footer_connect .connect .address {
            padding-right: 50px; }
          .footer .footer_background .footer_container .footer_connect .connect .social .social_icon {
            color: #76b3a8;
            margin-right: 20px; }
  .footer .credits {
    color: white;
    font-family: "raleway-regular";
    font-size: 12px;
    text-align: center;
    padding-top: 100px;
    padding-bottom: 25px; }
  @media screen and (max-width: 960px) {
    .footer img {
      min-width: 600px; }
    .footer .footer_background {
      padding: 100px 3% 0; }
      .footer .footer_background .footer_container {
        display: block; }
        .footer .footer_background .footer_container .site_map {
          justify-content: center; }
          .footer .footer_background .footer_container .site_map .site_map_column:nth-child(1) {
            padding-right: 10%; }
          .footer .footer_background .footer_container .site_map .site_map_column:nth-child(2) {
            padding-right: 0; }
        .footer .footer_background .footer_container .footer_connect {
          text-align: center;
          padding: 0;
          margin-top: 50px; }
          .footer .footer_background .footer_container .footer_connect .news_letter {
            text-align: center; }
            .footer .footer_background .footer_container .footer_connect .news_letter .signup_form {
              justify-content: center; }
          .footer .footer_background .footer_container .footer_connect .connect {
            justify-content: center;
            margin-top: 50px; }
            .footer .footer_background .footer_container .footer_connect .connect .address {
              padding-right: 4%; }
            .footer .footer_background .footer_container .footer_connect .connect .social {
              padding-left: 4%; }
              .footer .footer_background .footer_container .footer_connect .connect .social .social_icon {
                margin: 5px; }
      .footer .footer_background .credits {
        padding-top: 50px; } }
