.desktop_navigation {
  position: relative;
  width: 100%;
  background: #8fc5bc;
  text-align: center;
  padding-top: 40px; }
  .desktop_navigation img {
    width: 300px; }
  .desktop_navigation .links {
    display: flex;
    width: 100%;
    justify-content: center;
    padding-top: 40px;
    font-family: "raleway-extrabold"; }
    .desktop_navigation .links li {
      font-size: 16px;
      transition: 0.1s;
      cursor: pointer;
      letter-spacing: 2px;
      color: white;
      padding: 0px 18px 25px; }
    .desktop_navigation .links .dropdown {
      position: relative; }
      .desktop_navigation .links .dropdown ul {
        min-width: 270px;
        display: none;
        position: absolute;
        top: 44px;
        left: 15px;
        text-align: left; }
        .desktop_navigation .links .dropdown ul li {
          padding: 20px;
          background: #8fc5bc;
          border-top: 1px solid white; }
          .desktop_navigation .links .dropdown ul li:hover {
            background: #76b3a8; }
      .desktop_navigation .links .dropdown:hover ul {
        display: block; }
